<template>
    <div class="binding">
    <header class="both">
        <div class="phone"><div class="phone-text"><span class="phone-line">手机绑定</span></div></div>
        <div class="phone" @click="routerToEmail"><div class="email-text">邮箱绑定</div></div>
    </header>
        <main>
            <div class="bindTips"><img class="icon" src="../assets/images/horn.png" alt="">绑定手机号/邮箱可获得20颗逆袭豆</div>
            <div class="mgt-15"><span class="input-phone">手 机：</span> <input class="input" type="number" oninput="if(value.length>11)value=value.slice(0,11)" onkeypress='return( /[\d]/.test(String.fromCharCode(event.keyCode) ) )' placeholder="输入11位手机号" v-model="phone"></div>
            <div class="code">
                <span class="input-phone">验 证：</span>
                <input class="input" type="number" oninput="if(value.length>6)value=value.slice(0,6)" placeholder="输入手机验证码" >
                <van-button v-bind:disabled="dis" @click="getCode" class="getCode" size="mini"><span  v-if="show" >获取验证码</span>
                <span v-if="!show" style="color: white">{{count}} s</span></van-button>
            </div>
           <div class="bindBtn"><img @click="bind" src="../assets/images/bindBtn.png" alt="" width="115px"></div>
        </main>
    </div>
</template>

<script>
    export default {
        name: "phoneBinding",
        data(){
            return{
                show:true,
                count:0,
                dis:false,
                phone:''
            }
        },
        methods:{
                routerToEmail() {
                    this.$router.push({name: 'emailBinding'});
                },
          bind:function () {

          },
            getCode() {
                var regPhone=11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
                if (this.phone==''){
                    this.$toast('请输入手机号');
                }
                else if (!regPhone.test(this.phone)) {
                    this.$toast('手机格式不正确');
                }else {
                    this.$toast('验证码已发送');
                    this.count = 60;
                    this.dis = true;
                    // if (!this.timer) {
                    //     this.count = this.count;
                    this.show = false;
                    this.timer = setInterval(() => {
                        if (this.count > 0 && this.count <= this.count) {
                            this.count--;
                        } else {
                            this.show = true;
                            this.dis = false;
                            clearInterval(this.timer);
                            this.timer = null;
                        }
                    }, 1000)
                }

                // }
            },
        },
    }
</script>

<style scoped>
    @import "../assets/css/bind.css";
    .getCode{
        position: absolute;
        right: 8px;
        top: 5px;
        width: 65px;
        color: white;
        background: #fcd32d;
        border: none;
        font-size: 13px;
    }
    .code .van-button--disabled{
        opacity: 1;
    }
</style>